@import '../../global/variables.scss';
:host{
    font-size: 10px;
  }
.dav {
    &__header {
        width: 100%;
        display: flex;
        height: 48px;
        align-items: center;
        background-color: white;
        justify-content: space-between;
        padding-left: 8px;
        padding-right: 16px;

        &__left {
            display: flex;
            &__icon {
                // // background-color: var(--red-primary);
                // // width: 32px;
                // // height: 32px;
                // // border-radius: 50%;
                display: flex;
                justify-content: center;
                align-items: center;
                width: 32px;
                    height: 32px;

                // &__img {
                //     width: 16px;
                //     height: 16px;
                // }
            }

            &__name {
                font-weight: $fw_bold;
                font-size: $font_large;
                margin-left: 8px;
                align-self: center;
            }
        }
        &__camera{
            width: 24px;
            height: 24px;
        }
    }
    &-flex{
        display: flex;
    }
    &-bottomSheet {
        display: flex;
        flex-direction: column;
        padding: 0 16px 16px;

        &-title {
            font-size: $font_larger;
        }

        &-align {
            align-self: center;
        }

        &-arrow {
            display: none;
            width: 24px;
            height: 24px;
            pointer-events: none;
            transform: rotateZ(90deg);
            align-self: center;


        }
        &-terms{
            font-size: $font_medium;
            color: $text_color;
            font-weight: $fw_medium;
        }
        &-close {
            display: none;
            width: 24px;
            height: 24px;
            cursor: pointer;
            background-color: transparent;

            @media screen and (min-width: 536px) {
                display: block;
            }
        }
        &-subTitle{
            display: flex;
            width: 100%;
            gap: 8px;
            font-size: $font_medium;
            color: black;
            font-weight: $fw_medium;
            margin-bottom: 16px;
        }
        
        &-item{
            display: flex;
            justify-content: space-between;
            width: 100%;
            &__content{
                display: flex;
                gap: 8px;
                &__img{
                    align-self: flex-start;
                }
                &__label{
                    align-self: center;
                    color: black;
                    font-size: $font_medium;
                }
            }
            &__arrow {
                transform: rotate(180deg);
                align-self: flex-start;
            }
        }
        &-afterHours{
            background-color: #f1f2f8;
            border-radius: 16px;
            padding: 16px;
            height: fit-content;
            &__title{
                @extend %defaultFont;
                color: black;
                font-size: $font_medium;
                font-weight: $fw_medium;
            }
            &__hours{
                @extend %defaultFont;
                color: #404040;
                font-size: $font_medium;
            }
        }
    }
}
.artificial-height{
    height: 156px;
}
.artificial-heightNoHour{
    height: 180px;
}